
* {
   -webkit-box-sizing: border-box;
           box-sizing: border-box;
   font-family: 'Montserrat', "sans-serif";
}
body {
    color: #fff;
    background: -webkit-gradient(linear, left top, right top, from(rgb(21, 153, 87)), to(rgb(21, 87, 153)));
    background: linear-gradient(to right, rgb(21, 153, 87), rgb(21, 87, 153));
}
#app {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
#app h1 {
    border: 1px solid #000;
    background-color: #0004;
    padding: 20px;
    margin-bottom: 60px;
}
#app h2 {
    border: 1px solid #000;
    background-color: #0004;
    padding: 20px;
    margin-top: 120px;
    position: absolute;
    margin-bottom: 30px;
}
.tooltip {
    position: relative;
    display: inline-block;
}
.tooltip .tooltiptext {
    visibility: hidden;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;

    /* Position the tooltip */
    position: absolute;
    z-index: 1;
}
.tooltip:hover .tooltiptext {
    visibility: visible;
}
.cmd{
    width: 300px;
    margin-top: 80px;
}
.btn {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-top: 10px;
    margin-bottom: 20px;
}
#tbRoad,#tbOut {
    border: 1px solid #000;
    background-color: #0004;
    padding: 20px;
    position: absolute;
    margin-bottom: 60px;
    margin-top: 200px;
}
tbRoad,tbOut, td, th, tfoot {
    border:solid 1px #000;
    padding:5px;
}
